@import url('https://fonts.googleapis.com/css2?family=Playwrite+CU:wght@100..400&family=Playwrite+HR&display=swap');
@import url('https://fonts.googleapis.com/css2?family=LXGW+WenKai+TC&display=swap');
/*font-family: "LXGW WenKai TC";中文字體*/
/*font-family: "Playwrite CU";英文字體*/


  
div#IntroduceOnelevel{ /*最上方*/
    background-color: #ccc8c0; /* 背景色 */
    padding: 10px; /* 內邊距 */
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.7); /* 陰影效果 */
    border-radius: 5px; /* 圓角 */
    height: 55px;
    z-index: 1000; /* 確保在其他內容上方 */
    position: fixed;
    width: 100%; /* 寬度佔滿整個視窗 */
   
  }
  div#Topicssecondbg{ /*第二層背景*/
    margin-top: 30px;
    width: 100%;
    height: 650px;
    background-image: url('./image/017.jpg');
    background-position: center; /* 背景图片居中 */
    background-repeat: no-repeat; /* 背景图片不重复 */
    background-size: cover;
    position: relative;
  
  }

  
div#Topicstitle{ /*標題 flex大小*/
    width: 25%;
    display: flex;
    z-index: 2;
    height: 50px;
   
  }

  div#Topicstitle2{ /*專題介紹*/
    width: 50%;
    display: flex;
    z-index: 2;
    justify-content: center;
    font-family: "LXGW WenKai TC";
    font-size: 32px;
    height: 50px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4); 
  }

  div#Topicssecondbg .overlay { /*透明層*/
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(200, 200, 200, 0.3); /* 半透明白色背景，透明度為0.6 */
    z-index: 1; /* 確保背景疊加層在內容後面 */
  }
  .motivationtitle{ /*動機&解決標題*/
    font-family: "LXGW WenKai TC";
    font-size: 22px;
    margin-left: 15px;
    margin-bottom: 10px;
    
  }
  .motivationtext{  /*動機內容*/
    font-family: "LXGW WenKai TC";
    font-size: 14px;
    text-align: justify; /* 兩端對齊 */
    text-justify: inter-word;
    
  } 
  
  .motivationtext2{ /*解決內容*/
    font-family: "LXGW WenKai TC";
    font-size: 14px;
    text-align: justify; /* 兩端對齊 */
    text-justify: inter-word;
    
  }
  div#upbg{ /*讓動機.解決文字在透明層之上*/
    z-index: 2;
  }

  div#Topicscontent{
    margin-top: 10px;
    height: 240px;
    padding-bottom: 0;
  }
.motivationphoto{ /*圖片*/
  display: flex;
  z-index: 2;
  justify-content: center;
}
.card{ /*圖片*/
  z-index: 2;
  margin-top: 60px;
  margin-right: 30px;
}
.card-text{ /*圖片的文字*/
  font-family: "LXGW WenKai TC";
  display: flex;
  justify-content: center;
  font-size: 18px;
}
.gif005{ /*gif005*/
  display: flex;
  justify-content: right;
}

